.question-form-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #f5f5f5;
}

.question-box {
  background-color: #fff;
  padding: 20px 30px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  width: 400px;
  text-align: center;
}

.question-text {
  font-size: 18px;
  font-weight: bold;
  margin-bottom: 20px;
}

.options-container {
  display: flex;
  flex-wrap: wrap; 
  gap: 10px; 
  justify-content: space-between; 
  margin-bottom: 20px;
}

.option {
  flex: 0 1 calc(50% - 10px); 
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding: 10px;
  background-color: #f8f9fa;
  border: 1px solid #ddd;
  border-radius: 5px;
  cursor: pointer;
  transition: none;
}

.option:hover {
  background-color: #e9ecef;
  box-shadow: none; 
}

.option.selected {
  background-color: #d1e7dd;
  border-color: #0f5132;
}

.option input {
  margin-right: 10px; /* Space between radio and label */
}

.option label {
  cursor: pointer;
}

.next-btn {
  margin-top: 20px;
  padding: 10px 20px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
  transition: none; /* Remove button animation */
}

.next-btn:hover {
  background-color: #0056b3;
}

/* Remove any additional unwanted transitions */
* {
  transition: none !important;
}
